___CSS_LOADER_EXPORT___.push([module.id, `.header {
    display: grid;
    grid-template-columns: 1fr 6fr 2fr;
    background-color: var(--main-color);
    color: var(--main-text-color);
}

.header-container {
    grid-area: 1 / 2 / 2/ 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    padding: 28px 0px;
}

.progress-container {
    display: flex;
    align-items: center;
    gap: 16px;
}

.progress {
    width: 600px;
    height: 40px;
    border: 2px solid white;
    border-radius: 1.5em;
    position: relative;
}

.progress::before {
    content: '';
    position: absolute;
    display: flex;
    left: 0.5em;
    top: 0.4em;
    bottom: 0.4em;
    width: calc(var(--width, 0) * 1%);
    max-width: calc((100% - 1em));
    background-color: white;
    border-radius: 1.5em;
}

.user-settings {
    grid-area: 1 / 3 / 2 / -1;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 40px 48px;
}

.user-settings svg {
    fill: var(--main-text-color, white);
    height: 30px;
}

.user-settings svg:hover {
    cursor: pointer;
}

.modal-open {
    padding: 28px;
    border: 2px solid black;
    top: 35%;
    left: 45%;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.modal-close {
    display: none;
}

.inputs div {
    display: flex;
    gap: 8px;
}

.finish {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.save, .discard, .reset {
    font-size: 0.85rem;
    cursor: pointer;
}

.save {
    color: green;
}

.discard {
    color: red;
}

.reset {
    color: orange;
}

#name-input {
    padding: 1px 3px;
}

.name-input-container {
    display: flex;
    align-items: center;
}

.confirm-reset {
    max-width: 200px;
    display: flex;
    align-items: center;
    color: orange;
    gap: 2px;
}

.confirm-reset > div {
    padding: 2px 6px;
    border-radius: 12px;
    color: white;
    background-color: orange;
    cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/header.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,kCAAkC;IAClC,mCAAmC;IACnC,6BAA6B;AACjC;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,uBAAuB;IACvB,oBAAoB;IACpB,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,aAAa;IACb,WAAW;IACX,UAAU;IACV,aAAa;IACb,iCAAiC;IACjC,6BAA6B;IAC7B,uBAAuB;IACvB,oBAAoB;AACxB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,qBAAqB;IACrB,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,mCAAmC;IACnC,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,QAAQ;IACR,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,mBAAmB;IACnB,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;IACnB,YAAY;IACZ,wBAAwB;IACxB,eAAe;AACnB","sourcesContent":[".header {\n    display: grid;\n    grid-template-columns: 1fr 6fr 2fr;\n    background-color: var(--main-color);\n    color: var(--main-text-color);\n}\n\n.header-container {\n    grid-area: 1 / 2 / 2/ 3;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 16px;\n    padding: 28px 0px;\n}\n\n.progress-container {\n    display: flex;\n    align-items: center;\n    gap: 16px;\n}\n\n.progress {\n    width: 600px;\n    height: 40px;\n    border: 2px solid white;\n    border-radius: 1.5em;\n    position: relative;\n}\n\n.progress::before {\n    content: '';\n    position: absolute;\n    display: flex;\n    left: 0.5em;\n    top: 0.4em;\n    bottom: 0.4em;\n    width: calc(var(--width, 0) * 1%);\n    max-width: calc((100% - 1em));\n    background-color: white;\n    border-radius: 1.5em;\n}\n\n.user-settings {\n    grid-